// src/data/cards.js
function importAll(r) {
  const imported = r.keys().map((key, index) => {
    const name = key
      .replace("./", "")
      .replace(/\.[^/.]+$/, ""); // 

    return {
      id: index + 1,
      name,
      image: r(key),
    };
  });

  //  
  console.log(" :", imported.map((c) => c.name));

  return imported;
}

const cards = importAll(
  require.context("../assets/cards", false, /\.(png|jpe?g|webp)$/)
);

export default cards;
